import React from 'react';

import Terminal from 'react-console-emulator';
import { findRenderedDOMComponentWithClass } from 'react-dom/cjs/react-dom-test-utils.production.min';
import './console.scss';

class Console extends React.Component {
    constructor(props) {
        super(props);
        this.terminal = React.createRef();
        this.welcomeMessage = 
"<pre>.-----------------------------------------------------------------------------.<br />\
|  ____                                              ###########              |<br />\
| / ___|  ___  ___ _   _ _ __ ___                   ##         ##             |<br />\
| \\___ \\ / _ \\/ __| | | | '__/ _ \\              m###*     m     *###m         |<br />\
|  ___) |  __/ (__| |_| | | |  __/_           ##*     ..#####,,     *##       |<br />\
| |____/ \\___|\\___|\\__,_|_|  \\___(_)        m##    .#############,    ##m     |<br />\
|   ____            _        _             ##    .###     #     ###,    ##    |<br />\
|  / ___|___  _ __ | |_ __ _(_)_ __       m#    ###     #####     ###    #m   |<br />\
| | |   / _ \\| '_ \\| __/ _` | | '_ \\      ##   .##       '#'      ###,   ##   |<br />\
| | |__| (_) | | | | || (_| | | | | |_    ##   ###   wwwww wwwww   ###   ##   |<br />\
|  \\____\\___/|_| |_|\\__\\__,_|_|_| |_(_)   ##   '##  w####   ####w  ##*   ##   |<br />\
|  ____            _            _       ,#*     *###*' *     * '*####     '#, |<br />\
| |  _ \\ _ __ ___ | |_ ___  ___| |_      ##,   *'*###,         ,###'`'   ,##  |<br />\
| | |_) | '__/ _ \\| __/ _ \\/ __| __|      *##       '*#########*'       ##'   |<br />\
| |  __/| | | (_) | ||  __/ (__| |_ _       ##.,#m                 m#,.##     |<br />\
| |_|   |_|  \\___/ \\__\\___|\\___|\\__(_)       *'  *#####m     m#####*  '*      |<br />\
|                                                    '*#######*'              |<br />\
'-----------------------------------------------------------------------------'</pre>SCP Network\nVersion 2.0.3.23\n\nType `help` for getting some hints.";
        this.state = {
            prompt: 'anonymous@scp:~$',
            inProgress: false,
            progress: '',
            loggedIn: false
        }
        this.commands = {
            login: {
                description: 'Login to the SCP Server with the given data.',
                fn: (username, password) => {
                    if(username === "jan" && password === "jessefaden") {
                        this.setState({loggedIn: true, prompt: 'jan@scp:~$'});
                        this.terminal.current.pushToStdout("Successfully logged in.");
                        this.terminal.current.pushToStdout("<br />");
                        return "### You got a new message!<br /><br />";
                    } else {
                        return "Login data incorrect. Try again.";
                    }
                }
            },
            messages: {
                description: 'View all messages.',
                fn: () => {
                    if(this.state.loggedIn) {
                        this.terminal.current.pushToStdout("--Unread:");
                        this.terminal.current.pushToStdout("1. Subject: New Case - SCP-1408");
                        this.terminal.current.pushToStdout("<br />");
                        this.terminal.current.pushToStdout("--Archive:");
                        this.terminal.current.pushToStdout("2. Subject: New Case - SCP-3008");
                        this.terminal.current.pushToStdout("3. Subject: Elevator repaired");
                        this.terminal.current.pushToStdout("4. Subject: The cake is a lie");
                        this.terminal.current.pushToStdout("<br />");
                        return 'Type `read %number%´ to read a message';
                    } else {
                        return "You have to login!";
                    }
                }
            },
            read: {
                description: 'Open the passed message e.g. ´read 1´',
                fn: (message) => {
                    if(this.state.loggedIn) {
                        if(message == 1) {
                            this.terminal.current.pushToStdout("<br />");
                            this.terminal.current.pushToStdout("#############");
                            this.terminal.current.pushToStdout("Subject: New Case - SCP-1408");
                            this.terminal.current.pushToStdout("<br />");
                            this.terminal.current.pushToStdout("Message:<br />");
                            this.terminal.current.pushToStdout("We got a new SCP! Redeem 8ZPMX-ZA878-K89W9 on Steam and have a look!");
                            this.terminal.current.pushToStdout("#############");
                            return '';
                        } else {
                            return "The message has been deleted!";
                        }
                    } else {
                        return "You have to login!";
                    }
                }
            },
            logout: {
                description: 'Log out from the SCP Server.',
                fn: () => {
                    this.setState({loggedIn: false, prompt: 'anonymous@scp:~$'});
                }
            }
        }
    }
    render() {
        return(
            <Terminal
                ref={this.terminal}
                className="console"
                contentClassName="console__content"
                promptLabelClassName="console__prompt"
                commands={this.commands}
                welcomeMessage={this.welcomeMessage}
                promptLabel={this.state.prompt}
                disabled={this.state.inProgress}
                locked={this.state.inProgress}
                hidePromptWhenDisabled={true}
                dangerMode={true}
            />
        );
    }
    componentDidMount() {
        this.connect();
    }
    login() {
        this.terminal.current.pushToStdout("Connection established");
        this.terminal.current.pushToStdout("<br />");
        this.terminal.current.pushToStdout("Enter username:");
    }
    connect() {
        this.setState({inProgress: true});
        this.terminal.current.pushToStdout("<br />");
        this.terminal.current.pushToStdout("<br />");
        var counter = 0;
        var interval = setInterval(() => {
            if(counter === 40) {
                clearInterval(interval);
                this.terminal.current.pushToStdout("<br />");
                this.terminal.current.pushToStdout("Connection established");
                this.setState({inProgress: false, progress: ''});
                this.terminal.current.pushToStdout("<br />");
                this.terminal.current.pushToStdout('Type `login %username% %password%` to sign in');
                this.terminal.current.pushToStdout("<br />");
            } else {
                this.setState({progress: this.state.progress + '.'}, () => this.terminal.current.pushToStdout("Connecting to SCP Server: " + this.state.progress));
                counter++;
            }
        }, 150);
    }
}

export default Console;